.base{
  margin-left: 70px;
  width: calc(100% -70px);
}

@media all and (max-width: 600px) {
  .base{
    margin-left: 8px !important;
    width: calc(100% - 8px) !important;
  }
}

.login-base {
  margin-top: 10%;
  text-align: center;
}
.login-logo {
  margin-top: 2%;
  margin-bottom: 2%;
  color: #ec6f18;
  font-weight: 600;
  font-size: 1.2rem;
}
.login-input {
  text-align: center;
  display: inline-block;
}
.input-password,
.input-user {
  display: block;
  text-align: left;
}
.input-user input,
.input-password input {
  width: 400px;
  border-bottom: 1.6px solid #266f94;
  font-size: 1rem;
  box-shadow: 0px 3px 3px -2px gray;
  margin-top: 3%;
  margin-bottom: 4%;
}

.input-user label,
.input-password label {
  color: #ec6f18;
  font-size: 1.4rem;
  font-weight: 400;
}
.input-btn {
  margin-top: 2%;
}
.input-btn a {
  background-color: #266f94;
  width: 300px;
  padding: 10px 30px;
  border-radius: 5px;
  box-shadow: 0px 5px 4px 1px gray;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  transition: .1s ease-in-out;
}

.input-btn a:active{
  background-color: #ec6f18;

}

.input-user svg,
.input-password svg {
  height: 26px;
  margin-right: 1.5%;
  margin-top: 5px;
}

.base {
  display: block;
  text-align: center;
}
.card-icon {
  transition: 400s linear;
  font-size: 7.2rem;
  font-weight: 800;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-icon svg {
  color: #266f94;
  height: 6.4rem;
  margin: 0;
}

a {
  color: #266f94;
}

a:active {
  color: #ec6f18;
}

.card-base {
  margin: 30px;
  display: inline-block;
  box-shadow: 0px 2px 4px 2px gray;
  width: 400px;
  height: 200px;
  display: inline-block;
  border-radius: 10px;
}
.card-base-title {
  text-align: left;
}
.card-base-title h4 {
  font-size: 1.1rem;
  font-weight: 600;
  margin-left: 1.1%;
  margin-bottom: 0;
  margin-top: 1%;
}


@media all and (max-width: 600px) {
  .card-base{
    width: calc(90%);
    margin: 10px;
  }
}
.bar-base{
    width: 70px;
    background-color: #266f94;
    height: calc(300vh);
    display: flex;
    float: left;
    transition: 700ms ease-in-out;

    overflow: hidden;
}
.bar-icons{
    margin-top: 40%;
}
.bar-icons a, .bar-icons svg{
    margin-bottom: 90%;
    margin-left: 50%;
    display: block;
    color: white;
    height: 28px;
}

a:active svg{
    color: #ec6f18;
}

@media all and (max-width: 544px) {
    .bar-base{
      width: 18px;
    }
    .bar-icons a, .bar-icons svg{
        margin-left: 2%;
        height: 20px;
        width: 17px;
    }
  }
  
.nav-base {
  width: 100vw;
