.base-table-permits label {
  display: block;
  position: relative;
  padding-left: 30px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 18px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.base-table-permits label input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 16px;
  width: 16px;
  border-radius: 5px;
  border: solid #ec6f18 1px;
  background-color: #eee;
}

.base-table-permits label:hover input ~ .checkmark {
  background-color: #ccc;
}

.base-table-permits label input:checked ~ .checkmark {
  background-color: #266f94;
}
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.base-table-permits label input:checked ~ .checkmark:after {
  display: block;
}

.base-table-permits label .checkmark:after {
  left: 5px;
  top: 3px;
  width: 3px;
  height: 5px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

